	module.exports = {"data":{"challengeNode":{"title":"Render HTML Elements to the DOM","description":["到目前为止，你已经了解到 JSX 是一种在 JavaScript 中编写可读 HTML 的便捷工具。在 React 中，我们可以使用它的的渲染 API（ReactDOM）将此 JSX 直接渲染到 HTML DOM。","ReactDOM 提供了一个简单的方法来将 React 元素呈现给 DOM，如下所示：<code>ReactDOM.render(componentToRender, targetNode)</code>，其中第一个参数是要渲染的 React 元素或组件，第二个参数是要将组件渲染到的 DOM 节点。","如你所料，必须在 JSX 元素声明之后调用<code>ReactDOM.render()</code>，就像你在使用变量之前必须声明它一样。","<hr>","代码编辑器有一个简单的 JSX 组件。使用<code>ReactDOM.render()</code>方法将该组件渲染到页面。可以将定义好的 JSX 元素直接作为第一个参数传入，并使用<code>document.getElementById()</code>来选择要渲染到的 DOM 节点，在这个挑战中，请渲染到 id 为<code>challenge-node</code>的<code>div</code>中。"],"challengeType":6,"fields":{"slug":"/front-end-libraries/react/render-html-elements-to-the-dom","blockName":"React","tests":[{"text":"常量<code>JSX</code>应该返回一个<code>div</code>元素。","testString":"assert(JSX.type === 'div', '常量<code>JSX</code>应该返回一个<code>div</code>元素。');"},{"text":"<code>div</code>应该包含一个<code>h1</code>标签作为第一个元素。","testString":"assert(JSX.props.children[0].type === 'h1', '<code>div</code>应该包含一个<code>h1</code>标签作为第一个元素。');"},{"text":"<code>div</code>应该包含一个<code>p</code>标签作为第二个元素。","testString":"assert(JSX.props.children[1].type === 'p', '<code>div</code>应该包含一个<code>p</code>标签作为第二个元素。');"},{"text":"提供的 JSX 元素应该渲染到 id 为<code>challenge-node</code>的 DOM 节点。","testString":"assert(document.getElementById('challenge-node').childNodes[0].innerHTML === '<h1>Hello World</h1><p>Lets render this to the DOM</p>', '提供的 JSX 元素应该渲染到 id 为<code>challenge-node</code>的 DOM 节点。');"}]},"required":[{"link":null,"raw":null,"src":"https://unpkg.com/react@16.4.0/umd/react.production.min.js"},{"link":null,"raw":null,"src":"https://unpkg.com/react-dom@16.4.0/umd/react-dom.production.min.js"}],"files":{"indexhtml":null,"indexjs":null,"indexjsx":{"key":"indexjsx","ext":"jsx","name":"index","contents":"const JSX = (\n  <div>\n    <h1>Hello World</h1>\n    <p>Lets render this to the DOM</p>\n  </div>\n);\n// change code below this line\n","head":"","tail":""}}}},"pathContext":{"challengeMeta":{"introPath":"","template":"<body><div id='root'></div><div id='challenge-node'></div>${ source || '' }</body>","required":[{"link":null,"raw":null,"src":"https://unpkg.com/react@16.4.0/umd/react.production.min.js"},{"link":null,"raw":null,"src":"https://unpkg.com/react-dom@16.4.0/umd/react-dom.production.min.js"}],"nextChallengePath":"/front-end-libraries/react/define-an-html-class-in-jsx","id":"5a24bbe0dba28a8d3cbd4c5f"},"slug":"/front-end-libraries/react/render-html-elements-to-the-dom"}}

